<template>
  <section id="instagram-block">
    <div class="content" :class="$mq">
      <!-- <h3>My Instagram Feed</h3> -->
      <div id="instafeed" :class="$mq" @click="showMultiple($event)" />
      <vue-easy-lightbox
        :visible="visible"
        :imgs="imgs"
        :index="index"
        @hide="handleHide"
      >
        <template slot="toolbar" slot-scope="props">
          <button v-show="false" @click="props.toolbarMethods.zoomIn">zoom in</button>
          <button v-show="false" @click="props.toolbarMethods.zoomOut">zoom out</button>
          <button v-show="false" @click="props.toolbarMethods.rotateLeft">Anticlockwise rotation</button>
          <button v-show="false" @click="props.toolbarMethods.rotateRight">clockwise rotation</button>
        </template>
      </vue-easy-lightbox>
      <form data-aos="fade-up" action="https://www.instagram.com/aleksandrs_sprudzans" target="_blank" rel="noopener">
        <button class="button" type="submit">Follow Me On Instagram →</button>
      </form>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
import Instafeed from '@/utils/instafeed.js'

export default {
  name: 'InstagramBlock',

  data () {
    return {
      visible: false,
      index: 0,
      imgs: []
    }
  },

  mounted () {
    axios
      .get('https://aleksandrs94-token-agent.herokuapp.com/token.json')
      .then(response => {
        // console.log(response.data.token)
        const userFeed = new Instafeed({
          accessToken: response.data.token,
          limit: 10,
          template: `<a class="click" data-aos="fade-up" target="_blank" rel="noopener" aria-label="{{caption}}">
                      <img class="insta" loading="lazy" title="{{caption}}" src="{{image}}" alt="{{caption}}" />
                    </a>`
        })
        userFeed.run()
      })
  },

  methods: {
    showMultiple ($event) {
      const imgTree = document.querySelectorAll('img.insta')
      for (let i = 0; i < imgTree.length; i++) {
        const obj = {}
        obj.src = imgTree[i].src
        obj.title = imgTree[i].title
        this.imgs.push(obj)
        if ($event.toElement.src === imgTree[i].src) {
          this.index = i // index of imageArray
        }
      }
      this.show()
    },
    show () {
      this.visible = true
    },
    handleHide () {
      this.visible = false
    }
  }
}
</script>

<style lang="scss">
@import '@/styles/_variables.scss';
@import '@/styles/_button.scss';
@import '@/styles/_mixins.scss';

#instagram-block {
  color: $grey-font;
  background-color: $grey-background;
  .content {
    padding: 20vh 0;
  }
}
#instafeed {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  padding-bottom: 30px;
  &.mobile {
    grid-template-columns: repeat(2, 1fr);
  }
  a {
    position: relative;
    box-sizing: border-box;
    background-color: $full-black;
    &::before {
      content: '';
      display: block;
      padding-top: 100%;
    }
    @include hover-supported() {
      cursor: pointer;
      z-index: 1;
    }
  }
  img {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: grayscale(100%);
    will-change: transform, scale;
    @include hover-supported() {
      animation: bounce .3s forwards;
    }
  }
}
@keyframes bounce {
  0% {
    filter: gray;
    filter: grayscale(100%);
    transform: scale(1,1);
  }
  100% {
    filter: none;
    filter: grayscale(0%);
    transform: scale(1.07, 1.07);
  }
}
</style>
